#nav-bar {
    display: flex;
    margin-right: var(--spacing-medium);
}

.nav-bar-button {
    text-decoration: underline;
    padding: var(--spacing-medium);
    padding-bottom: 0;
    cursor: pointer;
    margin-top: 0;
    margin-bottom: 0;
    color: #f5f5f5;
}

#push-right-but-push-left-small-screen {
    margin-left: auto;
}

@media screen and (max-width: 25rem) {
    #nav-bar {
        margin-left: var(--spacing-medium);
    }

    #push-right-but-push-left-small-screen {
        margin-left: 0;
        margin-right: auto;
    }
}