.deck-or-card-grid {
    display: grid;
    border-top-style: solid;
    border-top-width: 1px;
    border-color: var(--light-black-border);
    max-width: 36rem;
    /*  
    this would be used if I were to go back to using a save button 
    instead of doing an onBlur action to save for new cards and decks
        grid-template-columns: 5fr 1fr; 
    */
}

.new-item-grid {
    grid-template-columns: 1fr;
    /* margin-bottom: var(--spacing-medium); */
}

.existing-item-grid {
    grid-template-columns: 5fr 1fr;
    grid-auto-flow: dense;
}